<template>
  <div id="sixthTable">
   <table>
     <thead>
       <tr>
         <th v-for="col in columns" v-on:click="sortTable(col)" :value="col" :key="col">{{col}}
           <div class="arrow" v-if="col == sortColumn" v-bind:class="[ascending ? 'arrow_up' : 'arrow_down']"></div>
         </th>
       </tr>
     </thead>
     <tbody>
       <tr v-for="product in get_rows()" :key="product">
         <td v-for="col in columns" :value="product[col]" :key="product[col]">{{product[col]}}</td>
       </tr>
     </tbody>
  </table>
  <div class="pagination">
</div>
</template>

<script>
import axios from 'axios'
const api = axios.create({baseURL: 'http://localhost:8080'})
export default {
  mounted() {
    api
    .get('/getmf')
    .then(response => this.product=response.data)
  },
  methods: {
    "sortTable": function sortTable(col) {
      if (this.sortColumn === col) {
        this.ascending = !this.ascending;
      } else {
        this.ascending = true;
        this.sortColumn = col;
      }

      var ascending = this.ascending;

      this.product.sort(function(a, b) {
        if (a[col] > b[col]) {
          return ascending ? 1 : -1
        } else if (a[col] < b[col]) {
          return ascending ? -1 : 1
        }
        return 0;
      })
    },
    "num_pages": function num_pages() {
      return Math.ceil(this.product.length / this.elementsPerPage);
    },
    "get_rows": function get_rows() {
      var start = (this.currentPage-1) * this.elementsPerPage;
      var end = start + this.elementsPerPage;
      return this.product.slice(start, end);
    },
  },
  data() {
    return {
      currentPage: 1,
      ascending: false,
      sortColumn: '',
      product: "",
      elementsPerPage: 999,
    }
  },
  computed: {
    "columns": function columns() {
      if (this.product.length == 0) {
        return [];
      }
      return Object.keys(this.product[0])
    }
  },
}
</script>

